//variables
@import "~/src/assets/styles/variables";

.milestones {
  display: flex;
  flex-flow: column;
  row-gap: 60px;
  &__container {
    display: none;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.25rem;
    &.js--active {
      display: grid;
    }
    @media screen and (max-width: $lg) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: $md) {
      grid-template-columns: 1fr;
    }
  }
  &__card {
    cursor: pointer;
    padding: 1.875rem;
    box-shadow: $card-shadow;
    display: flex;
    flex-flow: column;
  }
  &__card-image {
    width: 100%;
    aspect-ratio: 1;
    object-fit: cover;
    margin-bottom: 1.875rem;
    flex-shrink: 0;
  }
  &__heading-container {
    column-gap: 16px;
    display: flex;
    min-height: 4.25rem;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__video-button {
    width: 2.5rem;
    height: 2.5rem;
    flex-shrink: 0;
  }
  &__heading {
    height: fit-content;
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  &__video-button-circle {
    transition: fill 0.2s ease;
    &:hover {
      fill: $dark-turquoise;
    }
  }
  &__tabs {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 10px;
    & .milestones__tab-button {
      margin: 0;
      padding: 2px;
      max-height: 24px;
      background-color: #ffffff00;
      &:hover {
        color: $primary-color;
        background-color: #ffffff00;
      }
    }
  }
  &__tab-switcher {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 24px;
    border: 2px solid $primary-color;
    border-radius: 12px;
    cursor: pointer;
    & div {
      width: 16px;
      height: 16px;
      background-color: $primary-color;
      border-radius: 8px;
      transition: transform 0.2s ease;
    }
  }
}